
/*상품 소개*/

.products {
  float: left;
  padding: 0 9px;
}

.products img {
  width: 249px;
  height: 320px;
}

.product_info {
  padding-top: 14px;
  padding-right: 10px;
}

.price_tag > div {
  float: left;
}

.sale {
  color:#FA622F;
  white-space: nowrap;
  font-weight: 800;
  font-size: 16px;
  margin-right: 7px;
}

.sale_price {
  font-weight: 800;
  font-size: 16px;
}

.product_title {
  margin-bottom: 8px;
  line-height: 1.45;
}

.product_title a{
  font-size: 16px;
  color: rgb(51, 51, 51);
  font-weight: 500;
}

.origin_price {
  content: "";
  display: block;
  clear: both;

  font-size: 14px;
  color: #999;
  text-decoration: line-through;
  margin-top: 2px;
}